.header{
	transition: background-color .3s ease, box-shadow .3s ease;
	&.is-sticky{
		position: fixed;
		left: 0;
		right: 0;
		z-index: 1000;
	}
	&.has-background{
		background-image: radial-gradient(transparent 1px,white 1px);
		backdrop-filter: saturate(50%) blur(4px);
		background-size: 6px 6px;
	}
	@media (min-width: 992px) {
		&.is-sticky{
			top: 0;
		}
		&.has-background{
			// background-color: white;
			box-shadow: 0 2px 42px 0 rgba(black, .08);
		}
	}
	.navigation-menu{
		padding: var(--space-xs-2) 0;
		// padding-top: 12px;
		// padding-bottom: 12px;
		display: flex;
		justify-content: space-between;
		ul{
			list-style-type: none;
			padding-left: 0;
			margin-bottom: 0;
			&:not(.social-list){
				li{
					display: inline-block;
					&:not(:last-child){
						margin-right: var(--space-s);
						@media (max-width: 576px) {
							margin-right: var(--space-xs-2);
						}
					}
					&.logo{
						$logo-size: 60px;
						width: $logo-size;
						height: $logo-size;
						img{
							width: 100%;
							height: 100%;
						}
						a{
							border-radius: 50%;
							display: inline-block;
							border: 2px solid;
							border-color: transparent;
						}
						&:hover{
							a{
								border-color: rgba($brand-600, 1);
							}
						}
					}
					&:not(:first-child){
						a{
							@include btn;
							@include btn-md;
							@include btn-ghost-brand;
						}
			        }
				}
			}
			&.social-list{
				@include flex-center-y;
				li{
					&:not(:last-child){
						margin-right: var(--space-s);
					}
				}
				@media (max-width: 576px) {
					display: none;
				}
			}
		}
	}
}

.is-mobile{
	.header{
		&.is-sticky{
			top: unset;
			bottom: 0;
			box-shadow: 0 -2px 42px 0 rgba(black, .08);
			background-image: radial-gradient(transparent 1px,white 1px);
			backdrop-filter: saturate(50%) blur(4px);
			background-size: 8px 8px;
		}
	}
}